* {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  background-color: #eff3f7;
  height: 100%;
}

html,
body,
.container {
  height: 100vh;
  display: flex;
  justify-content: center;
}

.loader {
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.movies {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  flex-wrap: wrap;
  padding: 50px;
  padding-top: 70px;
  width: 80%;
}

.movies .movie {
  width: 45%;
  background-color: white;
  margin-bottom: 70px;
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  font-weight: 300;
  padding: 20px;
  color: #6b6b6b;
  border-radius: 5px;
  box-shadow: 0 13px 27px -5px rgba(92, 92, 92, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.03), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
}

.movie img {
  position: relative;
  top: -50px;
  width: 100%;
  max-width: 150px;
  margin-right: 30px;
  box-shadow: 3px 4px 5px rgba(51, 51, 51, 0.25);
}

.movie .movie__title,
.movie .movie__year {
  font-weight: 300;
  margin: 0;
}

.movie .movie__title {
  margin-bottom: 5px;
  font-size: 24px;
  color: #3f3f3f;
}

.movie .movie__geners {
  padding: 0;
  margin: 0 20px 0 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  margin: 5px 0;
}

.movie__geners li {
  margin-right: 10px;
}
